.wrapItem {
  border: 1px solid rgb(25, 147, 147);
  border-radius: 20px;
  padding: 18px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: rgb(227, 241, 236);
}

.wrapTitle {
  margin-bottom: 24px;
}
.wrapTitle h4 {
  margin-bottom: 14px;
}

.wrapLink {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
